<template>
  <div id="app" v-bind:style="backgroundStyle">
    <div class="fullscreen-holder">
      <Art/>
    </div>
    <div class="fullscreen-holder">
      <Music/>
    </div>
  </div>
</template>

<script>
import Music from "./components/Music.vue";
import Art from "./components/Art.vue";
import { bus } from './main';


export default {
  name: "App",
  components: {
    Music, Art
  },
  data:function(){
    return {
      backgroundStyle:{
        'background-color':'darkgrey'
      }
    }
  },
  methods:{},
  mounted(){
    bus.$on('pattern-scheme-changed', scheme => {
      this.backgroundStyle= scheme.backgroundStyle;
    });
  }
};
</script>

<style>
html {
  margin:0;
}
body {
  margin:0px;
  
  user-select:none; 
}
#app {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height:100vh;
  position:relative;
}
.fullscreen-holder {
  position:absolute;
  top:0px;
  left:0px;
  right:0px;
  bottom:0px;
}

</style>
